.aboutme-container {
    text-align: left;
}

#engineering-interests-container {
    background-color: #213744;
    color: #fff;
    border-radius: 8px;
    padding: 1%;
}

#coach-headshot-container {
    width: 30%;
    display: inline-block;
    text-align: right;
}

#coach-headshot-image {
    width: 80%;
    height: 80%;
    border-radius: 10%;
}

#what-i-do-text-container {
    width: 65%;
    display: inline-block;
    vertical-align: middle;
}

#carousel-example-2 {
    width: 70%;
    margin: auto;
}

.carousel-inner {
    height: 300px !important;
}

.roadtrip-video {
    text-align: center;
}

.music-videos {
    text-align: center;
}

[data-toggle="collapse"] i:before {
    content: "\f068";
}
[data-toggle="collapse"].collapsed i:before {
    content: "\f067";
}
#accordion .card-header {
    margin-bottom: 8px;
}
#accordion .accordion-title {
    position: relative;
    display: block;
    padding: 8px 0 8px 50px;
    background: #213744;
    border-radius: 8px;
    overflow: hidden;
    text-decoration: none;
    color: #fff;
    font-size: 16px;
    font-weight: 700;
    width: 100%;
    text-align: left;
    transition: all 0.4s ease-in-out;
}
#accordion .accordion-title i {
    position: absolute;
    width: 40px;
    height: 100%;
    left: 0;
    top: 0;
    color: #fff;
    background: radial-gradient(rgba(33, 55, 68, 0.8), #213744);
    text-align: center;
    border-right: 1px solid transparent;
}
#accordion .accordion-title:hover {
    padding-left: 60px;
    background: #213744;
    color: #fff;
}
#accordion .accordion-title:hover i {
    border-right: 1px solid #fff;
}
#accordion .accordion-body {
    padding: 40px 55px;
}
#accordion .accordion-body ul {
    list-style: none;
    margin-left: 0;
    padding-left: 0;
}
#accordion .accordion-body li {
    padding-left: 1.2rem;
    text-indent: -1.2rem;
}
#accordion .accordion-body li:before {
    content: "\f10a";
    padding-right: 5px;
    font-family: "Flaticon";
    font-size: 16px;
    font-style: normal;
    color: #213744;
}
